/* 2018-11-02 */
html {
  height: 100%;
  width: 100%;
}

.bgVideo {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  background: url('background.jpg') no-repeat;
  background-size: cover;
  transition: 1s opacity;
}
video {
  object-fit: contain;
  vertical-align: baseline;
}
.videoCover {
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  background: rgba(0, 0, 0, 0.1);
}
.iconMenu {
  font-size: calc(.9em + 2.1vh);
}
.iconMenu > a {
  vertical-align: sub;
}
#hardcoreSwitch {
  font-size: calc(.45em + 1vh);
  vertical-align: middle;
}
.iconMenu > a > i {
  padding-right: 5px;
}

::placeholder {
 color: #ccc;
}

#login input[type="text"], #login input[type="password"] {
  background-color: transparent;
  color: #ccc;
  border: 0;
  padding: 0;
  margin: 0 0 0.5vw 0;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: calc(1em + 1vw);
  text-shadow: 1px 1px #000000;
  border-bottom: 2px solid;
  box-shadow: 0 1px #000;
}
.progressbar {
  position: absolute;
  top:0;
  left:0;
  width: 100%;
  height:20px;
}
.progressbarBar {
  height:5px;
  background-color: #ccc;
  border-width: 0 1px 1px 0;
  border-color: #000;
  border-style: solid;
}
#login button {
  background-color: #333;
  border: none;
  color: #ccc;
  padding: 5px 15px 5px 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: calc(.5em + 1vw);
  font-weight: 600;
  text-shadow: 1px 1px #000000;
  border-radius: 5px;
  border-width: 0 1px 1px 0;
  border-color: #000;
  border-style: solid;
}
#loginBtn {
  margin:20px 0 0;
}
#usernameInputWrapper:before {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f007";
  padding-right: 0.5vw;
  position: relative;
  font-size: calc(0.7em + 1vw);
  width: 1.2em;
  text-align: center;
  display: inline-block;
}
#username {
  position: relative;
}
#passwordInputWrapper:before {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f084";
  padding-right: 0.5vw;
  position: relative;
  font-size: calc(0.7em + 1vw);
  width: 1.2em;
  text-align: center;
  display: inline-block;
}
#password {
  position: relative;
}
#searchInputWrapper:before {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f002";
  padding-right: 0.5vw;
  position: relative;
  font-size: calc(0.7em + 1vw);
}

#login {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: calc(1em + 1vw);
  text-shadow: 1px 1px #000000;
  color: #ccc;
}
